import { useEffect, useRef, useState } from "react";
import style from "./style/style.module.css";
import { useTransform, useScroll, motion } from "framer-motion";
import { ColumnInner } from "../ColumnInner/component";

export const ParalaxStack = ({ stack }) => {
  const gallery = useRef(null);
  const [dimension, setDimension] = useState({ width: 0, height: 0 });

  const { scrollYProgress } = useScroll({
    target: gallery,
    offset: ["start end", "end start"],
  });
  const { height } = dimension;
  const y = useTransform(scrollYProgress, [0, 1], [0, height * 1]);
  const y2 = useTransform(scrollYProgress, [0, 1], [0, height * 2]);
  const y3 = useTransform(scrollYProgress, [0, 1], [0, height * 0.9]);
  const y4 = useTransform(scrollYProgress, [0, 1], [0, height * 1.5]);

  useEffect(() => {
    const resize = () => {
      setDimension({ width: window.innerWidth, height: window.innerHeight });
    };

    window.addEventListener("resize", resize);

    resize();

    return () => {
      window.removeEventListener("resize", resize);
    };
  }, []);

  const scale = useTransform(scrollYProgress, [0, 0.5, 1], [0.98, 0.99, 1]);

  return (
    <div className={style.container}>
      <motion.div ref={gallery} className={style.gallery} style={{ scale }}>
        <Column stack={[stack[0], stack[1], stack[2]]} y={y} />
        <Column stack={[stack[3], stack[4], stack[5]]} y={y2} />
        <Column stack={[stack[6], stack[7], stack[8]]} y={y3} />
        <Column stack={[stack[9], stack[10], stack[11]]} y={y4} />
      </motion.div>
    </div>
  );
};

const Column = ({ stack, y }) => {
  return (
    <motion.div className={style.column} style={{ y }}>
      <ColumnInner style={style} stack={stack} />
    </motion.div>
  );
};
