import classNames from "classnames";

export const ColumnInner = ({ stack, style }) => {
  return stack.map((src, i) => {
    return (
      <div key={i} className={style.item_contaner}>
        <div className={style.item}>
          <div className={style.block}>
            {src !== "Hooks" &&
              src !== "" &&
              src !== "SPA" &&
              src !== "PWA" && (
                <img
                  className={classNames(
                    style.img,
                    src === "React" && style.rotate
                  )}
                  src={`./img/${src}.svg`}
                  alt=""
                />
              )}

            <div className={style.text}>{src}</div>
          </div>
        </div>
      </div>
    );
  });
};
